export default {
  state: {
    fullNavList: [
      {
        name: "dashboard",
        title: "概览",
        icon: "3rd-dashboard",
        path: "/dashboard"
      },
      {
        name: "user",
        title: "用户管理",
        icon: "3rd-user",
        children: [
          {
            name: "analysis",
            title: "客户通道统计",
            path: "/analysis"
          },
          {
            name: "smsDetail",
            title: "短信发送查询",
            path: "/sms-detail"
          }
        ]
      }
    ],
    navList: []
  },
  getters: {
    navList: state => state.navList
  },
  mutations: {
    FILTER_NAV_LIST: (state, user) => {
      state.navList = state.fullNavList;
    }
  },
  actions: {
    FilterNavList: ({ commit }, user) => {
      commit("FILTER_NAV_LIST", user);
    }
  }
};
