import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";

Vue.use(Router);

export default new Router({
  mode: "hash",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      component: Home,
      children: [
        {
          path: "/",
          name: "home",
          redirect: "/dashboard"
        },
        {
          path: "/dashboard",
          name: "dashboard",
          meta: {
            title: "概览"
          },
          component: () => import("./views/Dashboard.vue")
        },
        {
          path: "/analysis",
          meta: {
            title: "客户通道统计",
            keepAlive: true
          },
          component: () => import("./views/Analysis.vue")
        },
        {
          path: "/sms-detail",
          meta: {
            title: "短信发送查询",
            keepAlive: true
          },
          component: () => import("./views/SmsDetail.vue")
        }
      ]
    }
  ]
});
